* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

*::before, *::after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: normal;
}

a {
    color: inherit;
    text-decoration: none;
}

button, input {
    font: inherit;
}

ul, ol {
    list-style: none;
}

body {
    font-size: 16px;
    line-height: 1.6;
    color: #444;
    background: #eceff2;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 0;
    margin: 0;
    /*overflow: scroll;*/
}

header {
    height: 10vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 16;
}

main {
    padding: 30px 50px;
    margin-top: 10vh;
    z-index: 8;
}

.ant-btn-primary:hover {
    background-color: #42b983;
}

.ant-list-item > li {
    border-bottom: 1px dashed #42b983;
}
